import React, {Component} from 'react';
import {Grid, Container, Typography} from "@material-ui/core"
import myUglyFace from "./images/myface.png"
import './App.css';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';

export default class About extends Component {
    constructor(props) {
        super(props);
        this.state = {aboutOpen: false}
    }
    render() {
        return (
        <Container className="about">
            <Grid 
              container
              spacing={3}
              >
                <Grid sm={6} container>
                    {this.renderPortrait()}
                    <Grid
                    container
                    justify="center"
                    alignContent="center"
                    alignItems="center">
                    <span className="iconAlign"><a href="https://www.linkedin.com/in/jack-penhale-ba918516a/" target="_blank">
                    <LinkedInIcon className="portIcon" fontSize="large"/>
                    </a></span>
                    <span className="iconAlign">
                    <a href="https://github.com/JackPenhale" target="_blank">
                    <GitHubIcon className="portIcon" fontSize="large"/>
                    </a>
                    </span>
                </Grid>
                </Grid>
                <Grid sm={6}>
                    {this.renderBio()}
                </Grid>

            </Grid>
        </Container>
        )
    }

    renderPortrait() {
        return (
            <img alt="my face" className="myFace" src={myUglyFace}/>
        )
    }

    renderBio() {
        return (
            <div className="aboutPadding">
                <Typography variant="body1" className="aboutBio">
                    Hello, welcome! My name is Jack Penhale, I am a recent graduate from Colorado State University whitch a Bachelors in Applied Computing technology. This site is a digital 
                    portfolio for me to showcase my development skills along with some of my photography. I have a passion for frontend development, photography and being creative.
                </Typography>
                <Typography variant="body1" className="aboutBio">
                    I am currently looking for an opportunity to work and add my skills to your team, learning and improving my skills in the process.
                </Typography>
            </div>
        )
    }
}