import React, {Component} from 'react';
import {Grid, Container, RadioGroup, Radio, Slide, makeStyles} from "@material-ui/core"
import './App.css';
import piImage from "./images/pi.png"
import tripImage from "./images/tripApp.png"
import hospitalImage from "./images/hospital.png"
import ProjectSlide from "./ProjectSlide"

export default class About extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showTripApp: true,
            showHospitalApp: false,
            showPiProject: false
        }
        
        this.handleChange = this.handleChange.bind(this);
        this.renderTripApp = this.renderTripApp.bind(this);
    }
    

    render() {
        return (
        <Container className="projects">
            <Grid 
              container
              spacing={3}
              >
                <Grid sm={12}>
                    {this.renderRadioButtons()}
                </Grid>
                <Grid sm={12}>
                {this.state.showTripApp && 
                    this.renderTripApp()}
                
                {this.state.showPiProject && 
                    this.renderPiProject()}
                {this.state.showHospitalApp && 
                    this.renderHospitalApp()}
                </Grid>
            </Grid>
        </Container>
        )
    }
    renderRadioButtons() {
        return (
        <div>
            <RadioGroup row defaultValue="tripApp" className="center">
                <Radio
                    value="tripApp"
                    color="secondary"
                    name="reactTripApp"
                    size="small"
                    onChange={this.handleChange}
                />
                <Radio
                    value="piProject"
                    color="secondary"
                    name="PiProject"
                    size="small"
                    onChange={this.handleChange}
                />
                <Radio
                    value="hospitalApp"
                    color="secondary"
                    name="FuelHospital"
                    size="small"
                    onChange={this.handleChange}
                />
        </RadioGroup>
      </div>
        );
    }
    handleChange(event)
    {
        switch(event.target.value) {
            case 'tripApp':
                const newState = this.state;
                newState.showTripApp = true;
                newState.showPiProject = false;
                newState.showHospitalApp = false;
                this.setState({newState});
                break;
            case 'piProject':
                const newerState = this.state;
                newerState.showTripApp = false;
                newerState.showPiProject = true;
                newerState.showHospitalApp = false;
                this.setState({newerState});
                break;
            case 'hospitalApp':
                const newestState = this.state;
                newestState.showTripApp = false;
                newestState.showPiProject = false;
                newestState.showHospitalApp = true;
                this.setState({newestState});
                break;
        }

    }
    renderTripApp()
    {
        return (
            <Slide direction="left" in={this.state.showTripApp}>
                <ProjectSlide
                    picture={tripImage}
                    title="Web Trip Application"
                    description="- 16 week project using GitHub and Scrum practices to develop a full-stack single page web application.
                    - Focus on responsive, mobile design.
                    - Developed front-end in React and Material UI.
                    - Developed RESTful API and back-end with Java and SQL."
                    languages="-Reactjs - Java -SQL"
                    github=""
                />
            </Slide>
        )
    }
    renderPiProject()
    {
        return (
            <Slide direction="left" in={this.state.showPiProject}>
                <ProjectSlide
                    picture={piImage}
                    title="Pi Playlist Selector"
                    description="- Used a heartbeat sensor connected to a Raspberry Pi that took the BPM of a users heartbeat and played a playlist of
                    songs with a matching BPM
                    - Developed using Python"
                    languages="-Python"
                    github=""
                />
            </Slide>
        )
    }
    renderHospitalApp()
    {
        return (
            <Slide direction="left" in={this.state.showHospitalApp}>
                <ProjectSlide
                    picture={hospitalImage}
                    title="Hospital Data Application"
                    description="- Web application that displayed hospital diagnosis-related groups and corresponding data
                    - Developed using FuelPHP and SQL
                    - Included login and comment system"
                    languages="-PHP -SQL"
                    github=""
                />
            </Slide>
        )
    }
}